import React, {useState, useEffect} from 'react';
import './SectionCard.css';
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Card from "./Card/Card";
import Toast from "react-bootstrap/Toast";
import SubmitButton from './SubmitButton/SubmitButton';
import { ExclamationIcon, PlusCircleIcon, EnterKey } from "../../icons/index";
import { useCartContext } from "../../Context/CartContext";

export default function SectionCard() {
  const { num,setNumState,IngresarAlsistema,show,
    setShow,showBloked,setShowBloked,handleDelete,dateCard,setDateCard } = useCartContext();
    
  
    // Get Values of DataBase MySQL
async function DoNumber() {    
  const dateOrder = {
    cardNumbers: num,
  };
  setNumState( ' ');
  const response = await fetch('/isValidateNumber', {
      method: 'post',
      headers: {
        'Accept': 'application/json',
        'Content-Type': 'application/json'
    },
      body: JSON.stringify({
        numberCard: dateOrder.cardNumbers
    })
  })
  const result = await response.json();

  if (result.length > 0 ){
    if (result[0].estado === 0){
      const date = { item: result[0].estado };
      setDateCard(date)
      console.log(result[0].estado);
      setShowBloked(true);
    }
    else if(result[0].estado === 1){
      console.log("Probando si la tarjeta está activa")
      const date = { isValid: true, id: "id", item: result };
      setDateCard(date)
    }
  }
  else if (result.length == 0){
    setShow(true);
  }
}

useEffect(() => {
  DoNumber()
 }, []); 

 const AddNumber = (val) => {
  const addmoney = num;
  
  if((addmoney.toString().length+1) <= 16){ //9 billones
      if(addmoney == 0){
          setNumState(val.target.value);
      } else {
          setNumState( addmoney + val.target.value);
      }
  }
};



    return (
      <Row className="mt-2">
        <Col sm="4">
          <Card 
              var1 = {num}
              DoNumber={DoNumber}
          />
        </Col>
        <Col sm="8">
        <div className="calcShow">
                    <div className="numbers">
                        <div className="numbersAnimation">
                            <button    onClick={AddNumber} value="1" className="number">1</button>
                            <button   onClick={AddNumber} value="2" className="number">2</button>
                            <button   onClick={AddNumber} value="3" className="number">3</button>    
                        </div>
                        <div className="numbersAnimation">
                            <button   onClick={AddNumber} value="4" className="number">4</button>
                            <button   onClick={AddNumber} value="5" className="number">5</button>
                            <button   onClick={AddNumber} value="6" className="number">6</button>
                        </div>
                        <div className="numbersAnimation">
                            <button   onClick={AddNumber} value="7" className="number">7</button>
                            <button   onClick={AddNumber} value="8" className="number">8</button>
                            <button   onClick={AddNumber} value="9" className="number">9</button>
                        </div>
                        <div className="numbersAnimation">
                            <button   onClick={handleDelete} value="delete" className="number">&lt;</button>
                            <button   onClick={AddNumber} value="0" className="number">0</button>
                             <button  onClick={DoNumber} className="number ok"> <EnterKey width="60px" height="33px" color="green" /> </button>
                        </div>
                    </div>             
                </div>
        </Col>
        <Col sm="12">
         {dateCard.item === 0 &&(
        <Toast
        className="toast"
        onClose={() => setShowBloked(false)}
        show={showBloked}
        delay={4000}
        autohide
      >
        <Toast.Header>
      <PlusCircleIcon width={"20px"}/>
      <strong className="mr-auto">Validation Card Number</strong>
    </Toast.Header>
        <Toast.Body>
        card existing but blocked
 <ExclamationIcon color={"red"} width={"20px"} />
        </Toast.Body>
      </Toast>
        )}

  
         {!dateCard.isValid &&(
        <Toast
        className="toast"
        onClose={() => setShow(false)}
        show={show}
        delay={4000}
        autohide
      >
        <Toast.Header>
      <PlusCircleIcon width={"20px"}/>
      <strong className="mr-auto">Validation Card Number</strong>
    </Toast.Header>
        <Toast.Body>
          Card invalid <ExclamationIcon color={"red"} width={"20px"} />
        </Toast.Body>
      </Toast>
        )}
        </Col>
      </Row>
    )
}
