import React, {useState} from"react";
import "./Retiro.css";
import Container from "react-bootstrap/Container";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Toast from "react-bootstrap/Toast";
import { Link } from 'react-router-dom';
import { Succesfull, PlusCircleIcon,EnterKey, ExclamationIcon } from "../icons/index";
import { useCartContext } from '../Context/CartContext';

function Retiro() {
    const { itemsCard, dateCard, numRetiro,setNumRetiro, AddNumberRetiro, handleDelete } = useCartContext();
    const [showMsgBalance, setShowMsgBalance] = useState(false);


    async function isSendMoney(){
            const dateOrder = {
              cash: numRetiro,
              cardDate: dateCard.item,
              codOperacion: 'retiro'
            };

            console.log(itemsCard.saldo);
            if(numRetiro > itemsCard.saldo){
              setShowMsgBalance(true);
            } 

            setNumRetiro( ' ');

            const response = await fetch('/isValidateBalance', {
              method: "post",
              headers: {
                "Accept": 'application/json',
                "Content-Type": 'application/json',
              },
              body: JSON.stringify({
                balance: dateOrder.cash,
                cardDate: dateOrder.cardDate,
                codOperator: dateOrder.codOperacion

              })
            })
            const result = await response.json();
            console.log("resultado del balance:");
            console.log(result);
    }

  return (
      <Container className="containerRetiro">
          <Row>
              <Col sm="12">
          <header>
            <h1 className="titleAnimationRetiro">Operaciones</h1>
          </header>
          <hr className="hrAnimationRetiro" />
          </Col>
          </Row>
            <Row>
          <Col sm="6">
            <div className="calcShowRetiro">
            <div className="numbersRetiro">
              <div className="numbersAnimationRetiro">
                <button onClick={AddNumberRetiro} value="1" className="number">
                  1
                </button>
                <button onClick={AddNumberRetiro} value="2" className="number">
                  2
                </button>
                <button onClick={AddNumberRetiro} value="3" className="number">
                  3
                </button>
              </div>
              <div className="numbersAnimationRetiro">
                <button onClick={AddNumberRetiro} value="4" className="number">
                  4
                </button>
                <button onClick={AddNumberRetiro} value="5" className="number">
                  5
                </button>
                <button onClick={AddNumberRetiro} value="6" className="number">
                  6
                </button>
              </div>
              <div className="numbersAnimationRetiro">
                <button onClick={AddNumberRetiro} value="7" className="number">
                  7
                </button>
                <button onClick={AddNumberRetiro} value="8" className="number">
                  8
                </button>
                <button onClick={AddNumberRetiro} value="9" className="number">
                  9
                </button>
              </div>
              <div className="numbersAnimationRetiro">
                <button onClick={handleDelete} value="delete" className="number">
                  &lt;
                </button>
                <button onClick={AddNumberRetiro} value="0" className="number">
                  0
                </button>
                <button onClick={()=> {}} className="numberRetiro okRetiro">
                <EnterKey width="60px" height="33px" color="green"/>
                </button>
              </div>
            </div>
          </div>
          </Col>
          <Col sm="6">

            <div className="showQuantityRetiro">
                <div className="buttonsRetiro">
                <button class="btn btn-outline-primary btn-lg"> Limpiar </button>
            <button onClick={isSendMoney} class="btn btn-outline-success ml-3 btn-lg"> Aceptar </button>
            <Link to="/">
            <button class="btn btn-outline-danger ml-3 btn-lg"> Salir </button>
            </Link>
                </div>
            <div className="quantityRetiro">
              <p className='quantity_sizeRetiro'>
              $  {numRetiro}
              </p>
              <hr className="quantity_hrRetiro" />
              {showMsgBalance &&(
            <Toast
            className="toast"
            onClose={() => setShowMsgBalance(false)}
            show={showMsgBalance}
            delay={4000}
            autohide
          >
            <Toast.Header>
          <PlusCircleIcon width={"20px"}/>
          <strong className="mr-auto">Validación de Saldo</strong>
        </Toast.Header>
            <Toast.Body>
            Saldo Insuficiente para esta operación <ExclamationIcon color={"red"} width={"20px"} />
            </Toast.Body>
          </Toast>
            )}
            </div>
          </div>
      
          </Col>
    </Row>
    </Container>
  );
}

export default Retiro;
